<template>
  <div>

      <div class="head-nav">
        <div class="project-title">
          <p>{{project.title}}</p>
        </div>
        <div class="back">
          <router-link to="/home">
              x
          </router-link>
        </div>
          
      </div>



      <div id="visual">
        <button v-on:click="component='barchart'">CHANGE TO BARCHART</button>
        <button v-on:click="component='timeline'">CHANGE TO TIMELINE</button>
        <button v-on:click="component='list'">CHANGE TO LIST</button>

        <keep-alive>  
          <component v-bind:id=id v-bind:is="component"></component>
        </keep-alive>
        

      </div>

      <div class="addButtonProject">
        <button class="entryButton" @click="showEntryModal = true">+</button>
      </div>

      <div class="addButtonProject">
        <button class="userButton" @click="showUserModal = true">U</button>
      </div>

      <entryModal v-bind:id=id v-if="showEntryModal" @close="showEntryModal = false">
      </entryModal>

      <userModal v-bind:id=id v-if="showUserModal" @close="showUserModal = false">
      </userModal>

      <p>
        {{id}}
      </p>
  </div>
</template>
<script>
import { projectRef } from "../firebase";
import { userRef } from "../firebase";

export default {
  name: "project",
  data() {
    return {
      component: "list",
      showEntryModal: false,
      showUserModal: false,
      email: "",
      project: {},
    };
  },
  props: ["id"],
  mounted: function() {
    this.$bindAsObject("project",projectRef.child(this.id))
  },
  methods: {
  },
  firebase: {
  }
};
</script>



